export default {
	props:['list', 'isMy'],
	data() {
		return {};
	},
	methods:{
		onClick(index) {
			this.$emit("edit", index)
		},
		getType(type){
			if(type==0) return '院级'
			if(type==1) return '校级'
			if(type==2) return '市级'
			if(type==3) return '省级'
			if(type==4) return '国家级'
			if(type==5) return '国际级'
			if(type==6) return '其他'
		}
	}
}
